const jwt='eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYwY2RkYjQ4ZGIyMjY5MTM2YzdjMjhkYyIsIm5hbWUiOiJUZXN0VXNlciIsImVtYWlsIjoidXRzdW5vbWl5YUBmZGVzLmNvbSIsImlhdCI6MTYzMjcxODM5NiwiZXhwIjoxNjMyNzM5OTk2fQ.eOkCVmt9Q-huinIKY_z3zAFql2QQAbiaLp2Y6-Gg6ww';

export default function useFetchFilter (url,field,id){
  const [data, setData] = useState([]);
  const [err, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setError(null);
        setData(null);
        setLoading(true);

        const response = await axios.get(
          API+url+ '?filter='+encodeURIComponent(`{"where":{"${field}":"${id}"}}`),{
            headers:{
              // 'Authorization':`Bearer ${jwt}`
              'Authorization':`Bearer ${localStorage.getItem("access_token")}`
            }
          }
        );
        setData(response.data); // 데이터는 response.data 안에 들어있습니다.
      } catch (e) {
        console.log(e)
        setError(e);
      }
      setLoading(false);
    };
    fetchData();
  }, [id]);
if(loading) 
return loading
else{
  return data;
}
}