
import React, { PureComponent } from 'react';
import { PieChart, Pie, Sector, Cell, ResponsiveContainer ,Tooltip,Legend} from 'recharts';


const COLORS = ['#33add6', '#ff813d', '#b6dd0a', '#33d633'];

const RADIAN = Math.PI / 180;

const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
      {`${(percent * 100).toFixed(0)}%`}
    </text>
  );
};

export default function PieShape (props) {
  let selectedStore=''
  if(props.selectedStore){
    selectedStore=props.selectedStore
  }
  

const data1 = props.data.filter(el=>el.dataType=='原材料仕入チェック')[0]
const data2 = props.data.filter(el=>el.dataType=='温度管理記録')[0]
const data3 = props.data.filter(el=>el.dataType=="トイレ清掃記録")[0]
const data4 = props.data.filter(el=>el.dataType=="その他")[0]

const data = [
  { name: '原材料仕入チェック', value:data1['sum'] },
  { name: '温度管理記録', value: data2['sum'] },
  { name: 'トイレ清掃記録', value: data3['sum'] },
  { name: 'その他', value: data4['sum'] },
];
    return (
      <React.Fragment>
        <div className="title text-center mb-3" style={{ fontWeight:"bold",fontSize:"18px" }}>
          <div className="mb-2">
            {selectedStore}  -   2021年 違反件数 
            </div>
            原材料仕入チェック	: <span className="text-danger mx-2">{data1['sum']}</span>件 / 温度管理記録 :<span className="text-danger mx-2">{data2['sum']}</span>件/ トイレ清掃 : <span className="text-danger mx-2">{data3['sum']}</span>件/ その他 :<span className="text-danger mx-2">{data4['sum']}</span>件
          </div>
      <ResponsiveContainer width="100%" height="100%">
        <PieChart width={400} height={400}>
          <Pie
            data={data}
            cx="50%"
            cy="50%"
            labelLine={false}
            label={renderCustomizedLabel}
            innerRadius={60}
            outerRadius={130}
            fill="#8884d8"
            dataKey="value"
            // label
          >
            {data.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
          <Tooltip />
          <Legend />
        </PieChart>
      </ResponsiveContainer>
      </React.Fragment>
    );
}
