import { useState, useEffect } from 'react';
import axios from 'axios';
// axios.defaults.baseURL = "https://ec2-13-127-174-162.ap-south-1.compute.amazonaws.com/api/";
// axios.defaults.withCredentials = true;
const API='http://ec2-13-127-174-162.ap-south-1.compute.amazonaws.com/api/';
export default function GetToken (email="utsunomiya@fdes.com",password="Test@123" ){
    
  const [token, setToken] = useState('');

  useEffect(() => {
    const fetchToken = async () => {
      try {
        // 요청이 시작 할 때에는 error 와 users 를 초기화하고
        // const response = await axios.get(
        //   API+'/stores'
        // );
        const response = await axios.post(
          API+'users/login',{
            "email": email,
            "password": password
          }
        );
        setToken(response.data.token); // 데이터는 response.data 안에 들어있습니다.
        if(response.data.token){
          localStorage.setItem("access_token",response.data.token);
        }
      } catch (e) {
          console.log(e)
      }
    };

    fetchToken();
  }, []);
  return token;
}