import React, { PureComponent } from 'react';
import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

// function Posts ( posts ){
export default function BarShape (props) {
let selectedStore=''
if(props.selectedStore){
  selectedStore=props.selectedStore
}
let dataType=''
if(props.dataType){
  dataType=props.dataType
}

const data1 = props.data.filter(el=>el.dataType=='原材料仕入チェック')[0]
const data2 = props.data.filter(el=>el.dataType=='温度管理記録')[0]
const data3 = props.data.filter(el=>el.dataType=="トイレ清掃記録")[0]
const data4 = props.data.filter(el=>el.dataType=="その他")[0]

const data = [
  {
    name: '1月',
    原材料: data1['01'], 
    温度: data2['01'],
    トイレ: data3['01'],
    その他: data4['01'],
  },
  {
    name: '2月',
    原材料: data1['02'], 
    温度: data2['02'],
    トイレ: data3['02'],
    その他: data4['02'],
  },
  {
    name: '3月',
    原材料: data1['03'], 
    温度: data2['03'],
    トイレ: data3['03'],
    その他: data4['03'],
  },
  {
    name: '4月',
    原材料: data1['04'], 
    温度: data2['04'],
    トイレ: data3['04'],
    その他: data4['04'],
  },
  {
    name: '5月',
    原材料: data1['05'], 
    温度: data2['05'],
    トイレ: data3['05'],
    その他: data4['05'],
  },
  {
    name: '6月',
    原材料: data1['06'], 
    温度: data2['06'],
    トイレ: data3['06'],
    その他: data4['06'],
  },
  {
    name: '7月',
    原材料: data1['07'], 
    温度: data2['07'],
    トイレ: data3['07'],
    その他: data4['07'],
  },
  {
    name: '8月',
    原材料: data1['08'], 
    温度: data2['08'],
    トイレ: data3['08'],
    その他: data4['08'],
  },
  {
    name: '9月',
    原材料: data1['09'], 
    温度: data2['09'],
    トイレ: data3['09'],
    その他: data4['09'],
  },
  {
    name: '10月',
    原材料: data1['10'], 
    温度: data2['10'],
    トイレ: data3['10'],
    その他: data4['10'],
  },
  {
    name: '11月',
    原材料: data1['11'], 
    温度: data2['11'],
    トイレ: data3['11'],
    その他: data4['11'],
  },
  {
    name: '12月',
    原材料: data1['12'], 
    温度: data2['12'],
    トイレ: data3['12'],
    その他: data4['12'],
  },
];


    return (
      <React.Fragment>
        <div className="title text-center mb-3" style={{ fontWeight:"bold",fontSize:"18px" }}>
          <div className="mb-2">
          2021年 - {selectedStore}  -  違反件数 :
          <span  className="mx-3">
          </span>
          
          {
            dataType=="原材料仕入チェック" &&<React.Fragment>
            原材料仕入チェック	- <span className="text-danger mx-3">{data1['sum']}件</span>
            </React.Fragment>
          }
          {
            dataType=="温度管理記録" &&<React.Fragment> 温度管理記録 -<span className="text-danger mx-2">{data2['sum']}件</span> </React.Fragment>
          }
          {
            dataType=="トイレ清掃記録" &&<div>トイレ清掃 - <span className="text-danger mx-2">{data3['sum']}件</span></div>
          }
          {
            dataType=="その他" &&<div>その他 -<span className="text-danger mx-2">{data4['sum']}件</span></div>
          }
          {
            dataType=="全項目を表示" &&<div>
          原材料仕入チェック	- <span className="text-danger mx-2">{data1['sum']}件</span> / 温度管理記録 -<span className="text-danger mx-2">{data2['sum']}件</span>/ トイレ清掃 - <span className="text-danger mx-2">{data3['sum']}件</span>/ その他 -<span className="text-danger mx-2">{data4['sum']}件</span>
          </div>
          }
          </div>
        </div>
        <ResponsiveContainer width="100%" height="100%">
        <BarChart
          width={500}
          height={300}
          data={data}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis  />
          <Tooltip />
          <Legend />
          {
            dataType=="原材料仕入チェック" &&
            <Bar dataKey="原材料" fill="#33add6" />
          }
          {
            dataType=="温度管理記録" &&
            <Bar dataKey="温度" fill="#ff813d" />
          }
          {
            dataType=="トイレ清掃記録" &&
            <Bar dataKey="トイレ" fill="#b6dd0a" />
          }
          {
            dataType=="その他" &&
            <Bar dataKey="その他" fill="#33d633" />
          }
          {
            dataType=="全項目を表示" &&
            <React.Fragment>
              <Bar dataKey="原材料" fill="#33add6" />
              <Bar dataKey="温度" fill="#ff813d" />
              <Bar dataKey="トイレ" fill="#b6dd0a" />
              <Bar dataKey="その他" fill="#33d633" />
            </React.Fragment>
          }
          </BarChart>
      </ResponsiveContainer>
      </React.Fragment>

    );
  }