import React, { useState } from 'react';
import { useForm ,Controller} from 'react-hook-form';
import DatePicker from "react-datepicker"

import {Button} from "react-bootstrap"

import "react-datepicker/dist/react-datepicker.css"



function Search(props) {
  let [region,setRegion]= useState(props.selectedRegion ? props.selectedRegion:'60cd6078061f9c0ee82403aa');

  const {value,control, register, handleSubmit, formState: { errors }} = useForm({ defaultValues: {
    firstName: "bill",
    lastName: "luo",
    email: "bluebill1049@hotmail.com"
  }});

    const formatDate = (current_datetime)=>{
      var year = current_datetime.getFullYear();
      var month = current_datetime.getMonth()+1;
      var format = year+"-"+(("00"+month.toString()).slice(-2));
      return format;
  }

  const onSubmit = (event) => {
    event.startDate=formatDate(event.startDate)
    event.endDate=formatDate(event.endDate)
    props.handleOnSubmit(event)
  };

  const handleOnChange = (event) =>{
    setRegion(region=event.target.value)
  } 

  return (
    <React.Fragment>

        <form onSubmit={handleSubmit(onSubmit)}>
        <div className="bg-light p-2 d-flex justify-content-between">
        <select 
          value={region}
          onChange={handleOnChange}
          name="region"
          className="py-1 me-3 w-100 rounded"
        >
        {
          props.regionData &&
          props.regionData.map((el,idx)=>
          <option key={idx} value={el.id}  >{el.name}</option>
          )
        }
        </select>
        {errors.region && errors.region.message}
        <select 
          name="store"
          className="py-1 me-3 w-100 rounded"
          {...register("store", {
          })}
        >
             <option >店舗 選択</option>
          { props.storeData && props.storeData !== undefined && 
          Object.values(props.storeData).filter(item=>item.regionId===region).map((el,idx)=>
          <option key={idx} value={el.id} selected={el.id== props.selectedStore}>{el.name}</option>
          )
        }
        </select>
        {errors.store && errors.store.message}
        <select 
          name="dataType"
          className="py-1 me-3 w-100 rounded"
          {...register("dataType", {
          })}
        >
          <option value="60cdc047db2269136c7c28ac" selected={ props.selectedData=="60cdc047db2269136c7c28ac"}>原材料仕入チェック</option>
          <option value="60cdc056db2269136c7c28ad" selected={ props.selectedData=="60cdc056db2269136c7c28ad"}>温度管理記録</option>
          <option value="60cdc065db2269136c7c28ae" selected={ props.selectedData=="60cdc065db2269136c7c28ae"}>トイレ清掃記録</option>
          <option value="60cdc09adb2269136c7c28b0" selected={ props.selectedData=="60cdc09adb2269136c7c28b0"}>その他</option>
          <option value="61349b3499e12e0b857cef82" selected={ props.selectedData=="61349b3499e12e0b857cef82"}>全項目を表示</option>
        </select>
        <select 
          name="charts"
          className="py-1 me-3 w-100 rounded"
          {...register("charts", {
          })}
        >
          <option value="boardType" selected={ props.selectedChart=="boardType"}>BoradType</option>
          <option value="barType" selected={ props.selectedChart=="barType"}>BarType</option>
          <option value="stackBarType" selected={ props.selectedChart=="stackBarType"}>StackBarType</option>
          <option value="pieType" selected={ props.selectedChart=="pieType"}>PieType</option>
       
        </select>
        {errors.store && errors.store.message}
      
       <Controller
  name={"startDate"}
  control={control}
  defaultValue={new Date('May 1,2021')}
  render={({ field: { onChange, value } }) => {
    return (
      <DatePicker
        className="p-2 rounded border-gray ez-input w-100"
        onChange={onChange}
        selected={value}
        dateFormat="MM/yyyy"
        showMonthYearPicker
        showFullMonthYearPicker
      />
    );
  }}
/>
      <span className="mx-2 align-self-center">
        ~
      </span>
      <Controller
  name={"endDate"}
  control={control}
  defaultValue={new Date('September 1,2021')}
  render={({ field: { onChange, value } }) => {
    return (
      <DatePicker
        className="p-2 rounded border-gray ez-input w-100"
        onChange={onChange}
        selected={value}
        dateFormat="MM/yyyy"
        showMonthYearPicker
        showFullMonthYearPicker
        // minDate={startDate}
      />
    );
  }}
/>
      
        <Button type="submit" className="mx-2 text-nowrap" >検索</Button>
        <Button type="button" className="text-nowrap" >リフレッシュ</Button>  
        </div>
      </form>
  
    </React.Fragment>
  );
}

export default Search;
