
export default function MonthCondition (start,end){
   
  let monthData=[];
  let a=Number(start.slice(-2))
  let b=Number(end.slice(-2))
  let c=b-a+1;
  for(let i=0; i<c; i++){
    let c=a+i;
    if(c>9){
      c= "0"+c
      console.log(c)
      c= (c.slice(-2))
      monthData.push(c)
    }
    else{
      c= "0"+c
      monthData.push(c)
    }
  }
  return monthData
}