import React from "react";
import { useTable, useGlobalFilter, useSortBy } from "react-table";

function MonthTable({ columns, data }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    setGlobalFilter
  } = useTable({ columns, data }, useGlobalFilter, useSortBy);

  return (
    <>
       <div className="title text-center my-3" style={{ fontWeight:"bold",fontSize:"18px" }}>
       年度別項目別違反件数のグラフです。詳細については、項目を選択した後、検索をご利用下さい。
      </div>
      <table {...getTableProps()} className="w-100 text-center">
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>
                  {column.render("Header")}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row,tridx) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}
              >
                {row.cells.map((cell,idx) => {
                  return(
                   idx === 0 ||idx ===row.cells.length-1?
                      <th  className={
                        tridx===0?"bg-blue":
                        tridx===1?"bg-orange":
                        tridx===2?"bg-yellow":
                        "bg-green"
                      } 
                       {...cell.getCellProps()}>{cell.render("Cell")}

                      </th>:
                      <td  className={
                        tridx===0?"bg-blue-td":
                        tridx===1?"bg-orange-td":
                        tridx===2?"bg-yellow-td":
                        "bg-green-td"
                      } 
                      {...cell.getCellProps()}>{cell.render("Cell")}

                      </td>
                    
                    )
                  })}
               
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  );
}

export default MonthTable;
