import React from 'react';

export default function Posts ( posts ){
  const tableData=Object.entries(posts)
  return (
    <React.Fragment>
      <tbody>
      {
      tableData[0][1].map((el,idx)=>
      <tr key={idx}>
        {
        el.map((item,index)=>
        <td key={index}>
        {item}
        </td>
        )}
     </tr>
    )}
    </tbody>
  </React.Fragment>
  );
};
