import axios from 'axios';
const API='http://ec2-13-127-174-162.ap-south-1.compute.amazonaws.com/api/';
const jwt='eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYwY2RkYjQ4ZGIyMjY5MTM2YzdjMjhkYyIsIm5hbWUiOiJUZXN0VXNlciIsImVtYWlsIjoidXRzdW5vbWl5YUBmZGVzLmNvbSIsImlhdCI6MTYzMjcxODM5NiwiZXhwIjoxNjMyNzM5OTk2fQ.eOkCVmt9Q-huinIKY_z3zAFql2QQAbiaLp2Y6-Gg6ww';

export default function useFetchFormFilter (url,storeId,typeId){
  const [data, setData] = useState([]);
  const [err, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setError(null);
        setData(null);
        setLoading(true);

        const response = await axios.get(
          // API+url+ '?filter=%7B%22where%22%3A%7B%22storeId%22%3A%2260cd60df061f9c0ee82403ae%22%2C%22formTypeId%22%3A%2260cdc047db2269136c7c28ac%22%7D%7D',{
            
          API+url+ '?filter='+encodeURIComponent(`{"where":{"storeId":"${storeId}","formTypeId":"${typeId}"}}`),{
            headers:{
              'Authorization':`Bearer ${localStorage.getItem("access_token")}`
              // 'Authorization':`Bearer ${jwt}`
            }
          }
        );
        setData(response.data); // 데이터는 response.data 안에 들어있습니다.
      } catch (e) {
        console.log(e)
        setError(e);
      }
      setLoading(false);
    };
    fetchData();
  }, [url,storeId,typeId]);
if(loading) 
return loading
else{
  return data;
}
}