import React from 'react';

export default function Form (props){
  let tableData=[];
  if(props.formData){
      // 그냥 임의로 첫번째 값에 대한 값 th 로 작성
      let data =props.formData[0];
      if(data){
        // 여기서 바로 검색시 아래 데이터 재구성 되게 리 렌더링
        props.setFormIdFilter(data.id)
        for(let i=0;i<data.sections.length;i++){
          data.sections[i].fields.map((el)=>
          tableData.push(el.text))
        }
      }
    }
  return( 
    <React.Fragment>
          <thead  className="bg-light">
            <tr>
              { 
              tableData.map((el,idx)=>
              <th key={idx}>
                {el}
              </th>
                )}
            </tr>
          </thead>
            
    </React.Fragment>
  );
}