import React,{useState} from 'react';
import Post from './Post';
import Pagination from './Pagination';

export default function Report (props){
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage, setPostsPerPage] = useState(10);

  const indexOfLast = currentPage * postsPerPage;
  const indexOfFirst = indexOfLast - postsPerPage;
 let searchMonth = props.searchMonth;
  let tableData=[];

  if(props.reportData){
    let data =props.reportData;
    if(data){
      for(let i=0;i<data.length;i++){
        let test = Object.values(data[i].answers)
        var cnt = test[1]
        let tablePass=[];

        if(cnt){
              if(cnt.length ===1){
           
                for(let j=0;j<2;j++){
                  if(j===0){
                    Object.values(test[0]).map(el=>
                    typeof(el) === typeof('string') ?
                      tablePass.push(el):
                      tablePass.push(' ')
                    )
                  } 
                  else{
                    if(test[1][j-1] !=undefined){
                      Object.values(test[1][j-1]).map(el=>
                        typeof(el) === typeof('string') ?
                        tablePass.push(el):
                        tablePass.push(' ')
                        )
                      }
                  }
                }
                let monthCondition=searchMonth.filter(el => el==tablePass[0].slice(5,7))
                if(monthCondition.length>0)
                {
                  tableData.push(tablePass)
                }
                tablePass=[];
              }
              
              else{
                for(let j=0;j<cnt.length;j++){

                  Object.values(test[0]).map(el=>
                  typeof(el) === typeof('string') ?
                  tablePass.push(el):
                  tablePass.push(' ')
                  )
                  Object.values(test[1][j]).map(el=>
                  typeof(el) === typeof('string') ?
                  tablePass.push(el):
                  tablePass.push(' ')
                  )
                  let monthCondition=searchMonth.filter(el => el==tablePass[0].slice(5,7))
                  if(monthCondition.length>0)
                  {
                    tableData.push(tablePass)
                  }
                  tablePass=[];
                }
              }
            }
        // else{
        //   console.log('case3 @!@#!@#')
        //   if(test[0]){

        //     test[0].map(el=>
        //       tableData.push(Object.values(el)))
        //     }
        //   }
      }
    }
  }

  function currentPosts(tmp) {
    let currentPosts = 0;
    currentPosts = tmp.slice(indexOfFirst, indexOfLast);
    return currentPosts;
  }

  return( 
    <React.Fragment>
      <Post post={currentPosts(tableData)}/>
      <Pagination postsPerPage={postsPerPage} totalPosts={tableData.length} paginate={setCurrentPage}></Pagination>
    </React.Fragment>
  );
}