import React, { useMemo,useState} from 'react';
import "../styles.css";
import Topnav from './components/Topnav';
import Search from './components/Search';
import Footer from './components/Footer';
import Form from './components/Table/Form';
import FormMonth from './components/Table/FormMonth';
import Report from './components/Table/Report';
import useFetch from './hooks/useFetch';
import useFetchFilter from './hooks/useFetchFilter';
import useFetchFormFilter from './hooks/useFetchFormFilter';
import BarShape from './components/Charts/Bar'
import PieShape from './components/Charts/Pie'
import StackBar from './components/Charts/StackBar'
import MonthTable from './components/MonthTable';
import Loading from './components/Loading';
import MonUncleanData from './MonUncleanData';
import MonthCondition from './MonthCondition';


function Home() {
  const [storeIdFilter,setStoreIdFilter] =useState('60cd60df061f9c0ee82403ae')
  const [formIdFilter,setFormIdFilter] =useState('6144508999e12e0b857cf40f')
  const [typeId,setTypeIdFilter] =useState('60cdc047db2269136c7c28ac')
  const [chart,setChart] =useState('boardType')
  const [startDate,setStartDate] =useState('2021-05')
  const [endDate,setEndDate] =useState('2021-09')
  const [formIdFilter1,setFormIdFilter1] =useState('6144508999e12e0b857cf40f')
  const [formIdFilter2,setFormIdFilter2] =useState('6144508999e12e0b857cf40f')
  const [formIdFilter3,setFormIdFilter3] =useState('6144508999e12e0b857cf40f')
  const [formIdFilter4,setFormIdFilter4] =useState('6144508999e12e0b857cf40f')
  
  const region=useFetch('regions')
  const store=useFetch('stores')
  const form=useFetchFormFilter('forms',storeIdFilter,typeId)
  const report=useFetchFilter('reports','formId',formIdFilter)
  const selectedRegion=useFetchFilter('stores','id',storeIdFilter)
  
  const form1=useFetchFormFilter('forms',storeIdFilter,'60cdc047db2269136c7c28ac')
  const form2=useFetchFormFilter('forms',storeIdFilter,'60cdc056db2269136c7c28ad')
  const form3=useFetchFormFilter('forms',storeIdFilter,'60cdc065db2269136c7c28ae')
  const form4=useFetchFormFilter('forms',storeIdFilter,'60cdc09adb2269136c7c28b0')
  const report1=useFetchFilter('reports','formId',formIdFilter1)
  const report2=useFetchFilter('reports','formId',formIdFilter2)
  const report3=useFetchFilter('reports','formId',formIdFilter3)
  const report4=useFetchFilter('reports','formId',formIdFilter4)

  const handleOnSubmit = (data)=>{
  setStoreIdFilter(data.store)
  setTypeIdFilter(data.dataType)
  setChart(data.charts)
  setStartDate(data.startDate)
  setEndDate(data.endDate)
}
const searchMonth =MonthCondition(startDate,endDate);
const monUncleanData=[
  MonUncleanData(report1,'60cdc047db2269136c7c28ac'),
  MonUncleanData(report2,'60cdc056db2269136c7c28ad'),
  MonUncleanData(report3,'60cdc065db2269136c7c28ae'),
  MonUncleanData(report4,'60cdc09adb2269136c7c28b0')
];

let sum1 =0;
let sum2 =0;
let sum3 =0;
let sum4 =0;
if(monUncleanData){
  sum1=monUncleanData[0].sum;
  sum2=monUncleanData[1].sum;
  sum3=monUncleanData[2].sum;
  sum4=monUncleanData[3].sum;
}
const monColumns = useMemo(
  () => [
    {
      accessor: "dataType",
      Header: "区分"
    },
    {
      accessor: "01",
      Header: "1月"
    },
    {
      accessor: "02",
      Header: "2月"
    },
    {
      accessor: "03",
      Header: "3月"
    },
    {
      accessor: "04",
      Header: "4月"
    },
    {
      accessor: "05",
      Header: "5月"
    },
    {
      accessor: "06",
      Header: "6月"
    },
    {
      accessor: "07",
      Header: "7月"
    },
    {
      accessor: "08",
      Header: "8月"
    },
    {
      accessor: "09",
      Header: "9月"
    },
    {
      accessor: "10",
      Header: "10月"
    },
    {
      accessor: "11",
      Header: "11月"
    },
    {
      accessor: "12",
      Header: "12月"
    },
    {
      accessor: "sum",
      Header: "合計"
    },
  ],
  []
);

const monData = useMemo(
  () =>monUncleanData,[monUncleanData]
);

let selectorFilter='';
if(selectedRegion){
let temp=selectedRegion[0]
if(temp != undefined && temp != true)
  selectorFilter=temp.regionId
  // test4=temp.id
}

let selectedStore=''
if(store!=true){
  if(Array.isArray(store)){
    if(store.length!=0){
      let test1=store.filter((el)=>el.id==storeIdFilter)
      if(test1){
        let test2=test1[0]
        if(test2){
          selectedStore=test2['name']
        }
      }
    }
  }
} 
let dataType='';
  
if(typeId){
if(typeId=="60cdc047db2269136c7c28ac"){
  dataType="原材料仕入チェック"
}
else if(typeId=="60cdc056db2269136c7c28ad"){
  dataType="温度管理記録"
}
else if(typeId=="60cdc065db2269136c7c28ae"){
  dataType="トイレ清掃記録"
}
else if(typeId=="60cdc09adb2269136c7c28b0"){
  dataType="その他"
}
else{
  dataType="全項目を表示"
}
}
// console.log('storeis############################',selectedStore,'########',dataType)
if(region===true||form === true || report=== true||form1===true||form2===true||form3===true||form4===true||report1===true||report2===true||report3===true||report4===true)
return <React.Fragment><Loading/></React.Fragment>
return (
        <React.Fragment>
          {form1&&report1&&
           <FormMonth formData={form1}  setFormIdFilter={setFormIdFilter1}  />
          }
          {form2&&report2&&
           <FormMonth formData={form2}  setFormIdFilter={setFormIdFilter2}  />
          }
          {form3&&report3&&
           <FormMonth formData={form3}  setFormIdFilter={setFormIdFilter3}  />
          }
          {form4&&report4&&
           <FormMonth formData={form4}  setFormIdFilter={setFormIdFilter4}  />
          }
          <div  style={{ margin:"0 150px" }}>
            <Topnav/>
            <Search regionData={region} storeData={store} handleOnSubmit={handleOnSubmit} selectedRegion={selectorFilter!=''&&selectorFilter}  selectedStore={storeIdFilter} selectedData={typeId} selectedChart={chart}  />
          
            <div className={chart === 'boardType'?"title text-center my-3":"d-none"} style={{ fontWeight:"bold",fontSize:"18px" }}>
            2021年  - {selectedStore}  -  違反件数	:
            <span className="ms-3">
            {dataType} 
            </span>
            -
              {dataType !='' &&
                dataType =='原材料仕入チェック'?
                <span className="text-danger m-2">
                {sum1}件
               </span> 
                :
                dataType =='温度管理記録'?
                <span className="text-danger mx-2">
                {sum2}件
               </span> 
                :
                dataType =='トイレ清掃記録'?
                <span className="text-danger mx-2">
                {sum3}件
               </span> 
                :
                dataType =='その他'?
                <span className="text-danger mx-2">
                {sum4}件
               </span> 
                 :
                <React.Fragment>
                   <br/>
                   原材料仕入チェック
                <span className="text-danger mx-2">
                 {sum1} 件
                </span>
               /  温度管理記録
                <span className="text-danger mx-2">
                {sum2} 件
                </span>
                / トイレ清掃記録
                <span className="text-danger mx-2">
                {sum3} 件
                </span>
                / その他
                <span className="text-danger mx-2">
                {sum4} 件
                </span>
                </React.Fragment>
              }
        </div>
            <div className={chart === 'boardType'?"mt-4":"d-none"}>
              <table className="w-100 text-center">
                {form &&dataType!="全項目を表示" &&
                <Form formData={form}  setFormIdFilter={setFormIdFilter}  />
                }
                {report && dataType!="全項目を表示" &&
                  <Report reportData={report} searchMonth={searchMonth}/>
                }
              </table>
            </div>
            <div className={chart === 'barType'?"mt-4 h-380":"d-none"}>
              <BarShape data={monUncleanData} dataType={dataType}  selectedStore={selectedStore}/>
            </div>
            <div  className={chart === 'stackBarType'?"mt-4 h-380":"d-none"}>
              <StackBar data={monUncleanData}selectedStore={selectedStore} />
            </div>
            <div  className={chart === 'pieType'?"mt-4 h-380":"d-none"}>
              <PieShape data={monUncleanData}selectedStore={selectedStore} />
            </div>
          <div>
            {report&&
              <MonthTable columns={monColumns} data={monData} />
            }
          </div>
          <Footer/>
          </div>
      </React.Fragment>
  );
}

export default Home;